.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 250px;
}

.Card-image {
  height: 220px;
}

.Card-sm {
  width: 240px;
  max-width: 240px;
}

.sort-by {
  text-align: left;
  display: inline;
}

.ul {
  list-style: none;
  display: inline-block;
  text-align: left;
}

.li {
  margin-right: 1em;
  display: inline-block;
  text-align: center;
}

.center {
  float: none;
  margin: 0 auto;
}

@media only screen and (max-width: 600px) {
  .ul {
    text-align: center;
  }
} 

.footer {
  text-align: center;
}

.footer-img {
  width: 40px;
  height: 40px;
}